.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  width: 100%;
  background-color: var(--Primary-Color);
  color: var(--Secondary-Color);
  font-family: 'Montserrat';
}

.navLinks {
  list-style: none;
  display: flex;
  gap: 32px;
  align-items: center;
  cursor: pointer;
}

.navLinks a {
  text-decoration: none;
  color: var(--Secondary-Color);
}

.navLinks a:is(:hover, :focus) {
  color: var(--Accent-Color-1);
}

.navLinks img {
  width: 30px;
}
